import React from "react";
import { Wrapper, Title, LogoList, LogoWrapper, Logo } from "./elements";

const columnCount = {
  3: {
    numColumns: 3,
    numColumnsBreakpointMedium: 3,
    numColumnsBreakpointSmall: 1,
  },
  4: {
    numColumns: 4,
    numColumnsBreakpointMedium: 4,
    numColumnsBreakpointSmall: 2,
  },
  5: {
    numColumns: 5,
    numColumnsBreakpointMedium: 3,
    numColumnsBreakpointSmall: 2,
  },
  6: {
    numColumns: 6,
    numColumnsBreakpointMedium: 3,
    numColumnsBreakpointSmall: 2,
  },
};

const getColumns = (totalCount) => {
  if (columnCount[totalCount]) {
    return columnCount[totalCount];
  } else {
    return {
      numColumns: totalCount,
      numColumnsBreakpointMedium: 3,
      numColumnsBreakpointSmall: 2,
    };
  }
};

const TrustedByStyled = ({
  title = "TRUSTED BY",
  logos,
  lightBackground = false,
}) => (
  <Wrapper lightBackground={lightBackground}>
    <Title lightBackground={lightBackground}>{title}</Title>
    <LogoList>
      {Object.keys(logos).map((company) => (
        <LogoWrapper key={company} {...getColumns(Object.keys(logos).length)}>
          <Logo src={logos[company]} />
        </LogoWrapper>
      ))}
    </LogoList>
  </Wrapper>
);

export default TrustedByStyled;
