import React from "react";
import styled from "@emotion/styled";

const ImageRowComponent = styled("img")({
  width: "100%",
  objectFit: "contain",
});

const ImageRow = ({ defaultSrc, mobile, mobileRetina, desktop }) => (
  <ImageRowComponent
    src={defaultSrc}
    srcSet={`
      ${mobile.path} ${mobile.width}w,
      ${mobileRetina.path} ${mobileRetina.width}w,
      ${desktop.path} ${desktop.width}w,
    `}
    sizes={`
      (max-width: 1680px) 100vw,
      1680px
    `}
  />
);
export default ImageRow;
