import React from "react";

import { ButtonType } from "components/reusable/atoms/buttons/constants";
import ApplyButton from "components/reusable/atoms/buttons/ApplyButton";
import InternalLinkPrimaryButton from "components/reusable/atoms/buttons/InternalLinkPrimaryButton";
import InternalLinkSecondaryButton from "components/reusable/atoms/buttons/InternalLinkSecondaryButton";
import { Row, Column } from "components/Grid.tsx";
import { Container, Wrapper } from "./elements";

const CTARow = ({ ctaList, padding }) => (
  <Container>
    <Row padding={30} gutterWidth={26}>
      <Column span={1} spanMedium={0} />
      <Column span={10} spanMedium={12}>
        <Wrapper wrapperPadding={padding}>
          {ctaList.map(({ type, text, url, companyType, analyticsPath }) => {
            if (type === ButtonType.APPLY) {
              return (
                <ApplyButton
                  key={text}
                  companyType={companyType}
                  analyticsPath={analyticsPath}
                >
                  {text}
                </ApplyButton>
              );
            } else if (type === ButtonType.INTERNAL_LINK_PRIMARY) {
              return (
                <InternalLinkPrimaryButton key={text} to={url}>
                  {text}
                </InternalLinkPrimaryButton>
              );
            } else if (type === ButtonType.INTERNAL_LINK_SECONDARY) {
              return (
                <InternalLinkSecondaryButton key={text} to={url}>
                  {text}
                </InternalLinkSecondaryButton>
              );
            } else {
              return null;
            }
          })}
        </Wrapper>
      </Column>
      <Column span={1} spanMedium={0} />
    </Row>
  </Container>
);

export default CTARow;
