import React from "react";

import ApplyButton from "components/reusable/atoms/buttons/ApplyButton";
import InternalLinkSecondaryButton from "components/reusable/atoms/buttons/InternalLinkSecondaryButton";
import { Row, Column } from "components/Grid.tsx";
import heroImgSrcLoRes from "assets/img/product/corporate-card/hero@1x.png";
import heroImgSrcHiRes from "assets/img/product/corporate-card/hero@2x.png";
import TrustedByStyled from "components/TrustedByStyled";
import clever from "assets/img/logos/not-brex/grayscale/light-text/clever.png";
import sofi from "assets/img/logos/not-brex/grayscale/light-text/sofi.png";
import flexport from "assets/img/logos/not-brex/grayscale/light-text/flexport.png";
import boxed from "assets/img/logos/not-brex/grayscale/light-text/boxed.png";

import {
  TitleWrapper,
  TitlePrimaryText,
  ButtonGroup,
  ContentWrapper,
  Content,
  Container,
} from "./elements";

const titleContent = [
  {
    type: "secondary",
    text: "The corporate card built for",
  },
  {
    type: "primary",
    text: "modern enterprise",
  },
];

const trustedByProps = {
  title: "TRUSTED BY",
  logos: [clever, sofi, flexport, boxed],
  darkBackground: true,
};

const Title = ({ titleContent }) => (
  <TitleWrapper>
    {titleContent.map((content, index) => {
      let text = content.text;
      if (index < titleContent.length - 1) {
        text += " ";
      }

      return content.type === "primary" ? (
        <TitlePrimaryText key={text}>{text}</TitlePrimaryText>
      ) : (
        <span key={text}>{text}</span>
      );
    })}
  </TitleWrapper>
);

const Hero = () => (
  <Container>
    <Row padding={30} gutterWidth={26}>
      <Column span={1} spanMedium={0} />
      <Column span={11} spanMedium={12}>
        <ContentWrapper
          imgSrcLoRes={heroImgSrcLoRes}
          imgSrcHiRes={heroImgSrcHiRes}
        >
          <Content>
            <Title titleContent={titleContent} />
            <ButtonGroup>
              <ApplyButton analyticsPath="product.corporate-card.hero.signup">
                GET STARTED
              </ApplyButton>
              <InternalLinkSecondaryButton to="/contact-us">
                CONTACT SALES
              </InternalLinkSecondaryButton>
            </ButtonGroup>
          </Content>
        </ContentWrapper>
      </Column>
    </Row>
    <TrustedByStyled {...trustedByProps} />
  </Container>
);

export default Hero;
